import { Avatar, Icon, Stack } from '@mui/material';
import React from 'react';
import MyAvatar from '../assets/images/13-1.jpg';
import GitHubIcon from '@mui/icons-material/GitHub';

const Menu = (prop) => {
    const myGitHub = () => {
        window.location.href = 'https://github.com/bachikevin';
    }
    return ( 
        <Stack style={{ width: '100%' }} alignItems="center" textAlign="center">
            <Avatar sx={{ height: 100, width: 100 }} src={MyAvatar}></Avatar>
            <span>{prop.test}</span>
            <GitHubIcon onClick={myGitHub}/>
        </Stack>   

    )

}
export default Menu;